import React from 'react'
import ReactDOM from 'react-dom'

import Header from '../Header'
import Gallery from '../Gallery'
import '../../styles/text.css'
import '../../styles/home.css'
import createButton from '../../resources/createButton.png'
import galleryButton from '../../resources/GalleryButton.png'
import uploadButton from '../../resources/uploadButton.png'

export default class Home extends React.Component {
    constructor(params){
        super(params);
    }

    render(){

        return (
            <div>
                <Header></Header>
                <Gallery></Gallery>
                <div className='content' ref='content'>
                    <div className='item'>
                        <img src={createButton}></img>
                        <h1>Избери галерия</h1>
                        <h4>Изберете подходящата атмосфера за вашета изложба от дъстъпните зали</h4>
                    </div>
                    <div className='item'>
                        <img src={galleryButton}></img>
                        <h1>Постави снимки</h1>
                        <h4>Изберете подходящата атмосфера за вашета изложба от дъстъпните зали</h4>
                    </div>
                    <div className='item'>
                        <img src={uploadButton}></img>
                        <h1>Публикувай</h1>
                        <h4>Публикувайте и споделете, направената от вас експозиция, с потенциални купувачи</h4>
                    </div>
                    <h1>Какво е VShow?</h1>
                    <h4>
                        VShow е онлайн платформа за създаване на 3D експозиции на вашите картини, 3D модели 
                    </h4>
                </div>
            </div>
        )
    }
}