import React from 'react'
import ReactDOM from 'react-dom'
import '../styles/header.css'
import logo from '../resources/logo2.png'
import Menu from './Menu'
import {Link} from 'react-router-dom'

class Header extends React.Component {
    
    constructor(params) {
        super(params);

        this.state = {showHeader: false, text: '', index: 0};
        this.showHeader = this.showHeader.bind(this);
        this.lastScroll = 0;

    }
    componentDidMount() {
        window.addEventListener('scroll', this.showHeader);

        
    }
    componentWillUnmount() {
        window.removeEventListener('scroll', this.showHeader);
    }
    showHeader(e) {
        let scroll = window.pageYOffset;
        if(scroll >= 0) this.setState({showHeader: true});
        else this.setState({showHeader: false});

        if(this.scrolling !== null){
            clearTimeout( this.scrolling );
        }
            

        // ReactDOM.findDOMNode(this.refs.hologram).style.paddingLeft = Math.min(100+scroll/7, 160) + 'px';
        // ReactDOM.findDOMNode(this.refs.hologram).style.paddingTop = Math.max(100-scroll/4, 5) + 'px';
        // ReactDOM.findDOMNode(this.refs.hologram).style.width = Math.max(220-scroll/4, 100) + 'px';
        // ReactDOM.findDOMNode(this.refs.hologram).style.height = Math.max(160-scroll/4, 70) + 'px';

    }
    render() {
        let showHeader = this.state.showHeader ? '':'nav-up';


        return (
            <div>
                <header className={showHeader}>
                    <img src={logo}  ref='hologram'></img>
                    <Menu></Menu>
                <Link to='/login'>Login/Register</Link>
                </header>
            </div>
        );

    }
}
export default Header;